/*
* @param direction vertical or horizontal
* @param position type of positioning to apply (relative/absolute)
*/

/*
* @param $selector css selector on which to apply the icon. Can be '&' if the icon should be applied on the current element;
* @param $size size of the icon to use
*/

.coveo-waiting-for-first-query .CoveoSearchbox .CoveoSearchButton {
  z-index: 0;
}

.CoveoSearchbox.coveo-inline {
  overflow: hidden;
}

.CoveoSearchbox.coveo-inline .magic-box {
  overflow: visible;
}

.CoveoSearchbox .CoveoSearchButton {
  float: right;
  z-index: 2;
  position: relative;
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
}

.CoveoSearchbox .magic-box {
  border: 1px solid #BCC3CA;
  border-radius: 2px;
}

.CoveoSearchbox .magic-box .magic-box-clear:before {
  border-left: none;
}

.CoveoSearchbox .magic-box .magic-box-clear .magic-box-icon:before {
  content: '';
  display: inline-block;
  background-repeat: no-repeat;
  overflow: hidden;
  text-indent: 19px;
  background-image: url(../bin/image/spritesNew.png);
  background-position: -1075px -582px;
  width: 19px;
  height: 19px;
  background-size: 1094px 1025px;
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .CoveoSearchbox .magic-box .magic-box-clear .magic-box-icon:before {
    background-image: url(../bin/image/retinaNew.png);
    background-size: 1094px 1025px;
  }
}

.CoveoSearchbox .magic-box .magic-box-input {
  border-radius: 2px;
}

.CoveoSearchbox .magic-box .magic-box-input > input {
  color: #67768B;
}

.CoveoSettings ~ .CoveoSearchbox {
  margin-right: 76px;
}

.magic-box .magic-box-input .magic-box-underlay .magic-box-ghost-text,
.magic-box.magic-box-hasFocus .magic-box-input .magic-box-underlay .magic-box-ghost-text {
  display: none;
}




