/*
* @param direction vertical or horizontal
* @param position type of positioning to apply (relative/absolute)
*/

/*
* @param $selector css selector on which to apply the icon. Can be '&' if the icon should be applied on the current element;
* @param $size size of the icon to use
*/

.coveo-facet-more,
.coveo-facet-less {
  width: 100%;
  height: 15px;
  background: #F7F8F9;
  cursor: pointer;
  text-align: center;
}

.coveo-facet-more:hover,
.coveo-facet-more:focus,
.coveo-facet-less:hover,
.coveo-facet-less:focus {
  background: #BCC3CA;
  outline: none;
}

.coveo-facet-more {
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
  display: none;
}

.coveo-facet-more .coveo-icon {
  display: inline-block;
  background-repeat: no-repeat;
  overflow: hidden;
  text-indent: 13px;
  background-image: url(../bin/image/spritesNew.png);
  background-position: -753px -996px;
  width: 13px;
  height: 13px;
  background-size: 1094px 1025px;
  position: relative;
  top: 1px;
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .coveo-facet-more .coveo-icon {
    background-image: url(../bin/image/retinaNew.png);
    background-size: 1094px 1025px;
  }
}

.coveo-facet-more.coveo-active {
  display: block;
}

.coveo-facet-less {
  display: none;
}

.coveo-facet-less .coveo-icon {
  display: inline-block;
  background-repeat: no-repeat;
  overflow: hidden;
  text-indent: 13px;
  background-image: url(../bin/image/spritesNew.png);
  background-position: -783px -996px;
  width: 13px;
  height: 13px;
  background-size: 1094px 1025px;
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .coveo-facet-less .coveo-icon {
    background-image: url(../bin/image/retinaNew.png);
    background-size: 1094px 1025px;
  }
}

.coveo-facet-less.coveo-active {
  display: block;
}

.coveo-facet-footer.coveo-facet-empty {
  display: block;
  visibility: hidden;
}

.coveo-facet-footer {
  background: #F7F8F9;
  min-height: 15px;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
}




