.coveo-input {
  position: relative;
  margin-top: 35px;
}

.coveo-input input {
  width: 100%;
  height: 34px;
  padding: 0;
  font-size: 15px;
  background-color: transparent;
  box-shadow: none;
  border: 0;
  border-bottom: 1px solid rgba(79, 86, 88, 0.4);
  border-radius: 0;
  outline: 0;
  transition: all 0.2s ease;
}

.coveo-input input:focus:not([readonly]),
