.coveo-pager-list {
  padding-left: 0;
  margin: 0;
  display: inline-block;
}

.coveo-pager-list-item {
  border: 1px solid #BCC3CA;
  border-radius: 2px;
  margin: 5px 6px;
  padding: 4px 8px;
  list-style: none;
  display: inline-block;
  vertical-align: baseline;
  color: #009ddc;
  font-size: 14px;
  cursor: pointer;
}

.coveo-pager-list-item.coveo-active,
.coveo-pager-list-item:hover {
  color: #CDDEE9;
  background-color: #1D4F76;
}

.coveo-pager-list-item.coveo-active a,
.coveo-pager-list-item:hover a {
  color: #CDDEE9;
}

.coveo-pager-previous,
.coveo-pager-next {
  margin: 5px 6px;
  list-style: none;
  display: inline-block;
  vertical-align: baseline;
  cursor: pointer;
  border: none;
  padding: 0;
}

.coveo-pager-previous:hover,
.coveo-pager-next:hover {
  color: #009ddc;
  background-color: white;
}

.coveo-pager-next > a > span {
  display: inline-block;
  background-repeat: no-repeat;
  overflow: hidden;
  text-indent: 22px;
  background-image: url(../bin/image/spritesNew.png);
  background-position: -990px -934px;
  width: 22px;
  height: 22px;
  background-size: 1094px 1025px;
  vertical-align: middle;
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .coveo-pager-next > a > span {
    background-image: url(../bin/image/retinaNew.png);
    background-size: 1094px 1025px;
  }
}

.coveo-pager-previous > a > span {
  display: inline-block;
  background-repeat: no-repeat;
  overflow: hidden;
  text-indent: 22px;
  background-image: url(../bin/image/spritesNew.png);
  background-position: -961px -906px;
  width: 22px;
  height: 22px;
  background-size: 1094px 1025px;
  vertical-align: middle;
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .coveo-pager-previous > a > span {
    background-image: url(../bin/image/retinaNew.png);
    background-size: 1094px 1025px;
  }
}

.CoveoPager {
  margin-top: 0px;
  margin-left: 25px;
  display: inline-block;
}




