.CoveoSearchButton {
  color: white;
  border: 1px solid #bcc3ca;
  text-decoration: none;
  text-align: center;
  vertical-align: middle;
  overflow: hidden;
  height: 50px;
  width: 60px;
  cursor: pointer;
}

.CoveoSearchButton:before {
  content: '';
  vertical-align: middle;
  height: 100%;
  display: inline-block;
}

.CoveoSearchButton > .coveo-icon {
  vertical-align: middle;
  display: inline-block;
  background-repeat: no-repeat;
  overflow: hidden;
  text-indent: 18px;
  background-image: url(../bin/image/spritesNew.png);
  background-position: -1075px -922px;
  width: 18px;
  height: 18px;
  background-size: 1094px 1025px;
  animation: none;
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .CoveoSearchButton > .coveo-icon {
    background-image: url(../bin/image/retinaNew.png);
    background-size: 1094px 1025px;
  }
}

.CoveoSearchButton:hover > .coveo-icon {
  display: inline-block;
  background-repeat: no-repeat;
  overflow: hidden;
  text-indent: 18px;
  background-image: url(../bin/image/spritesNew.png);
  background-position: -1075px -616px;
  width: 18px;
  height: 18px;
  background-size: 1094px 1025px;
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .CoveoSearchButton:hover > .coveo-icon {
    background-image: url(../bin/image/retinaNew.png);
    background-size: 1094px 1025px;
  }
}

.coveo-executing-query .CoveoSearchButton > .coveo-icon,
.coveo-executing-query .CoveoSearchButton:hover > .coveo-icon {
  display: inline-block;
  background-repeat: no-repeat;
  overflow: hidden;
  text-indent: 16px;
  background-image: url(../bin/image/spritesNew.png);
  background-position: -630px -996px;
  width: 16px;
  height: 16px;
  background-size: 1094px 1025px;
  -webkit-animation-name: coveo-spin;
  -moz-animation-name: coveo-spin;
  animation-name: coveo-spin;
  -webkit-animation-timing-function: linear;
  -moz-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-duration: 1s;
  -moz-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
  -moz-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .coveo-executing-query .CoveoSearchButton > .coveo-icon,
  .coveo-executing-query .CoveoSearchButton:hover > .coveo-icon {
    background-image: url(../bin/image/retinaNew.png);
    background-size: 1094px 1025px;
  }
}




