.CoveoMatrix {
  margin: 10px 5px 10px 5px;
}

.coveo-matrix-title {
  text-align: center;
  font-size: 14px;
  font-weight: bold;
  margin: 10px 0 10px 0;
  color: #1D4F76;
  font-size: 16px;
}

.coveo-matrix {
  display: table;
  table-layout: fixed;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  border-spacing: 0;
  border-collapse: collapse;
}

.coveo-matrix-row {
  display: table-row;
}

.coveo-matrix-row .matrix-first-column {
  text-align: left;
}

.matrix-header-row,
.matrix-total-row {
  font-weight: bold;
  color: #1D4F76;
  font-weight: bold;
  height: 45px;
  background-color: #F7F8F9;
}

.matrix-total-row {
  font-weight: bold;
}

.coveo-matrix-cell {
  display: table-cell;
  padding: 5px;
  text-align: right;
  overflow: hidden;
  vertical-align: middle;
  border: 1px solid #BCC3CA;
}

.coveo-matrix-cell:hover {
  cursor: pointer;
}

.coveo-matrix-unimportant {
  color: lightgrey;
}

.matrix-results-preview-container {
  position: absolute;
  z-index: 1;
  border: 1px solid #BCC3CA;
  border-radius: 2px;
  background-color: white;
}

.matrix-results-preview-container .coveo-result-frame {
  border-bottom: 1px solid #BCC3CA;
  text-align: left;
  overflow: hidden;
  padding: 5px;
}

.matrix-results-preview-container .coveo-result-cell {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}




