.CoveoResultsPerPage {
  margin: 0 0 0 15px;
  display: inline-block;
  float: right;
}

.coveo-results-per-page-text {
  padding-left: 0;
  color: #BCC3CA;
}

.coveo-results-per-page-list {
  padding-left: 0;
  margin: 0;
  display: inline-block;
}

.coveo-results-per-page-list-item {
  border: 1px solid #BCC3CA;
  border-radius: 2px;
  margin: 5px 6px;
  padding: 4px 8px;
  list-style: none;
  display: inline-block;
  vertical-align: baseline;
  color: #009ddc;
  font-size: 14px;
  cursor: pointer;
  padding: 4px;
}

